<template>
    <div class="content-table">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
                <router-link to="/category/add">
                <el-button icon="el-icon-check" style="float: right; padding: 3px 0" type="text">Assets</el-button>
                </router-link>
            </div>
            <el-row>
                <el-col>
                    <el-form :inline="true" class="form-input-center" label-position="left" :model="formUpload" ref="formUpload" :rules="rules" label-width="200px">
                    <el-form-item label="Application">
                        <el-select style="width: 350px" v-model="DataApplication" placeholder="Activity zone">
                            <el-option :key="i"
                                    v-for="(tr, i) in application" :data="tr" :label="tr.name_applications" :value="[tr]">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="Category" prop="category">
                        <el-select v-if="ct" style="width: 350px" v-model="category" placeholder="Activity zone">
                            <el-option :key="i"
                                    v-for="(tr, i) in categoryData"
                                    :data="tr" :label="tr.name_categories" :value="tr.id_categories">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="Assets Images">
                        <el-radio-group v-model="formUpload.radio">
                            <el-radio label="1" :disabled ="assetsCount">Assets Upload</el-radio>
                            <el-radio label="2">Url ( http://asset.com/image.png )</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="Name Image" v-if="formUpload.radio == 2">
                        <el-input style="width: 350px" placeholder="Please input Name Image" v-model="formUpload.name"></el-input>
                    </el-form-item>
                    <el-form-item label="Assets Url" v-if="formUpload.radio == 2">
                        <el-input style="width: 350px" placeholder="Please input url asset" v-model="formUpload.url"></el-input>
                    </el-form-item>
                    <el-form-item label="Image Upload" v-else>                                
                        <el-upload
                        class="upload-demo"
                        :action="`http://localhost:8000/api/v2/${this.abc}`"
                        drag
                        ref="upload"
                        :file-list="fileList"
                        :limit="5"
                        list-type="picture"
                        :on-success="handleAvatarSuccess"
                        :auto-upload="false"
                        multiple>
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
                        <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 1024kb And Max 5 Image</div>
                        </el-upload>
                    </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <el-row>
                <el-col>
                    <div class="btn-input-add">                        
                    <el-button size="small" @click="submitForm()">Submit</el-button>
                    <el-button size="small" type="warning">Reset</el-button>
                    </div>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>

import axios from '../../axios.js'

// import router from '@/router'

export default {
    data() {
        var validateCategory = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('Please input Category'));
          } else {
            callback();
          }
        };
      return {
        abc : null,
        DataApplication : [],
        ct : false,
        application : [],
        category : null,
        formUpload: {
            radio : '1',
            url : '',
            name : ''
        },
        fileList : [],
        formData : new FormData(),
        assetsCount : false,
        rules: {
            category : [
                { validator: validateCategory, trigger: 'blur' }
            ],
        },
      }
    },
    computed : {
        baseUrl () {
            const path = this.$route.path
            return path[0]
        },
        headerCard () {
            return this.$route.name
        }
    },
    methods : {
        handleAvatarSuccess() {
            this.$notify({
                        title: 'Success',
                        message: 'Berhasil',
                        type: 'success',
                        offset: 100
                });
        },
        submitForm() {
            if (this.formUpload.radio == 1) {
                this.$refs.upload.submit()
                
            } else {
                const obj = {
                    id_applications : this.DataApplication[0].id_applications,
                    category : this.category,
                    type : this.formUpload.radio,
                    name : this.formUpload.name,
                    url : this.formUpload.url

                }
                axios({
                    method: 'post',
                    url: 'upload/image/url',
                    data: obj,
                    headers: {
                    'Authorization': `Bearer ${localStorage.getItem('accessToken')}`
                    }
                }).then((response) => {
                if (response.data.success == true) {
                    // router.push('/category')
                    this.$notify({
                        title: 'Success',
                        message: Response.data.message,
                        type: 'success',
                        offset: 100
                    });
                } else {
                    this.loading = false
                    this.$notify.error({
                        title: 'Error',
                        message: 'Invalid Data Input',
                        offset: 100
                    })
                }
                })  
            }
        }
    },
    watch : {
        category () {
            this.abc = `upload/${this.DataApplication[0].id_applications}/${this.category}/image`
        },
        DataApplication () {
            
            this.category = null
            if (!this.DataApplication) {
                this.ct = false
            } else {
                this.ct = true
                this.categoryData = this.DataApplication[0].category_assets
                // this.countData = this.DataApplication[0].image_assets
                const count = Object.keys(this.DataApplication[0].image_assets).length
                // this.assetsCount(count)
                if (count <= this.DataApplication[0].limit_applications) {
                    this.assetsCount = false
                    this.formUpload.radio = '1'
                } else {
                    this.assetsCount = true
                    this.formUpload.radio = '2'
                }
                
            }
        }
    },
    async created () {
        const accessToken = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        }
        await axios.get(`upload/${this.$store.state.userDataActive.id}/show`, accessToken)
            .then((response) => {    
            this.application = response.data.data
        })
    }
        
}
</script>

<style>
.upload-margin {
    margin: 0 auto;
}
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
.form-input-center {
    margin: 0 auto;
    width: 700px;
}
.btn-input-add {
    margin: 0 auto;
    width: 250px;
}
</style>